@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Roboto+Mono:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  overflow: hidden;
  box-sizing: border-box;
  background-color: rgb(28, 19, 39);
  color: white;
  height: 100vh;
  display: flex;
}
.bar{
  height: 100vh;
  width: 15vw;
  background-color: rgba(63, 61, 90, 0.5);
  font-family: 'Roboto Mono', monospace;
  padding: 2rem;
  font-weight: 300;
}



.container{width:40vw}.dashboard{border-radius:30px;padding:10px;margin:30px;box-shadow:0 0 25px #3f3d5a,0 0 25px #3f3d5a,0 0 5px #38355c;width:85vw;height:70vh;display:flex;flex-direction:row;box-sizing:border-box;display:flex;justify-content:space-around;align-items:center}h1{font-family:'Roboto Mono', monospace;letter-spacing:3px;margin:2rem}

